import {AfterViewInit, Component, Injector, OnInit} from '@angular/core';
import DataSource from 'devextreme/data/data_source';
import {BaseComponent} from '../../../@calls/components/base/base.component';
import {Validators} from '@angular/forms';

@Component({
    selector: 'app-users',
    templateUrl: './users.component.html',
    styleUrls: ['./users.component.scss']
})
export class UsersComponent extends BaseComponent implements OnInit, AfterViewInit {
    isLoading = false;
    dataSource: any;
    userForm: any;
    departments: any;
    departmentSelected = false;
    add = true;
    userId = '';
    type = '';
    errors = [];

    constructor(injector: Injector) {
        super(injector);
        this.userForm = this.fb.group({
            username: ['', Validators.compose([Validators.required])],
            login_id: ['', Validators.compose([Validators.required])],
            email: ['', Validators.compose([Validators.required])],
            password: ['', Validators.compose([Validators.required])],
            type: ['', Validators.compose([Validators.required])],
            dep_id: ['']
        });
    }

    ngOnInit(): void {
        this.getUsers();
        this.getDepartments();
    }

    ngAfterViewInit() {
        this.changeSelected();
    }

    deleteItem(item): void {
        console.log(item);
        this.isLoading = true;
        this.userService.removeUser(item.user_id).subscribe((res) => {
            console.log(res);
            this.getUsers();
            this.utilService.showDeletedSuccessfullyToast();
            this.isLoading = false;
        }, (err) => {
            console.log(err);
            this.isLoading = false;
            this.utilService.showConnectionErrorToast();
        });
    }


    getUsers(): void {
        this.userService.getUsers().subscribe((res) => {
            this.dataSource = new DataSource(res.content.data);
        });
    }

    resetForm() {
        const j = {
            login_id: '',
            username: '',
            email: '',
            password: '',
            type: '',
            dep_id: '',
        };
        this.userForm.setValue(j);
        this.add = true;
        this.departmentSelected = false;
        setTimeout(() => {
            this.changeSelected();
        }, 1000);
    }

    updateItem(data): void {
        this.add = false;
        const j = {
            username: data.username,
            login_id: data.login_id,
            email: data.email,
            password: data.password,
            type: data.type,
            dep_id: data['dep_id'] ? data['dep_id'] : ''
        };
        this.departmentSelected = !!data['dep_id'];
        this.userId = data.id;
        this.type = data.type;
        console.log(data);
        // this.dep_id = data.id;
        this.userForm.setValue(j);
    }

    changeSelected() {
        document.getElementById('my-type').addEventListener('change', (evt: any) => {
            console.log('You selected: ', evt.target.selectedIndex);
            this.departmentSelected = +evt.target.selectedIndex === 0;
        });
    }

    addUser(form, f): void {
        this.isLoading = true;
        this.errors = [];
        form['company_id'] = this.authService.currentUser.company_id;
        form['name'] = form.first_name + ' ' + form.last_name;
        if (this.add) {
            this.authService.register(form).subscribe((res) => {
                console.log(res);
                this.isLoading = false;
                if (res.result === 'success') {
                    f.resetForm();
                    this.getUsers();
                    document.getElementById('close').click();
                    this.utilService.showAddedSuccessfullyToast();
                } else {
                    const error = res.error_description;
                    let key;
                    for (key in error) {
                        console.log('Key: ' + key);
                        console.log('Value: ' + error[key]);
                        console.log(typeof error[key]);
                        if (typeof error[key] === 'string') {
                            if (error[key] !== 'error') {
                                this.errors.push(error[key]);
                            }
                        } else if (typeof error[key] === 'object') {
                            if (error[0] === 'error') {
                                this.errors.push('LoginID already exist !');
                            } else {
                                this.errors.push(error[key][0]);
                            }
                        } else {
                            this.errors.push('LoginID already exist !');
                        }
                    }
                }
            }, (err) => {
                this.isLoading = false;
                console.log(err);
                this.utilService.showConnectionErrorToast();
            });
        } else {
            form['id'] = this.userId;
            form['location'] = this.type;
            this.userService.updateUser(form).subscribe((res) => {
                console.log(res);
                this.isLoading = false;
                f.resetForm();
                this.getUsers();
                document.getElementById('close').click();
                this.utilService.showAddedSuccessfullyToast();
            }, (err) => {
                this.isLoading = false;
                console.log(err);
                this.utilService.showConnectionErrorToast();
            });
        }
    }

    getDepartments(): void {
        this.departmentService.getDepartments().subscribe((res) => {
            this.isLoading = false;
            this.departments = res.content.data;
        }, (err) => {
            this.isLoading = false;
            console.log(err);
            this.utilService.showConnectionErrorToast();
        });
    }
}
